import './navbar.css';
import React, { useState } from 'react';
import logo from './navbarfiles/logo.png';
import search from './navbarfiles/search.png';
import cart from './navbarfiles/cart.png';
import Modal from 'react-bootstrap/Modal'
import Button from 'react-bootstrap/Button';
import 'bootstrap/dist/css/bootstrap.min.css';
import prod01 from './navbarfiles/prod01.png';
import prod02 from './navbarfiles/prod02.png';
import prod03 from './navbarfiles/prod03.png';
import mais from './navbarfiles/mais.png';
import menos from './navbarfiles/menos.png';
import cross from './navbarfiles/cross.png';

function Navbar(){

    const [show, setShow] = useState(false);
      
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    return(
        <>


<div className="header">
    <img className="logo" src={logo}/>

    <div className="navbar">
        <div className="conteiner">
            <div className="sing">
                <ul>
                <li><a href="#">Entrar</a></li>
                <li><a>|</a></li>
                <li><a href="#">Cadastrar-se</a></li>
                </ul>
            </div>

            <div className="menu">
                <ul>
                    <li><a href="#">SAPATOS</a></li>
                    <li><a href="#">BOLSAS</a></li>
                    <li><a href="#">ACESSÓRIOS</a></li>
                    <li><a href="#">OFF</a></li>
                </ul>
            </div>

            <div className="menu2">
                <div className="searchbox">
                    <div className="search"><img src={search} /></div>
                    <div className="searchtxt"><p>Busca</p></div>
                </div>

                <Button className="btncart" variant="primary" onClick={handleShow}>
                    <div className="cartbox">
                        <img className="cart" src={cart} />
                        <div className="carttxt"><p>0</p></div>
                    </div>  
                </Button>


                <Modal className="modbody" show={show} onHide={handleClose} animation={false}>
                    <Modal.Header className="navmodalheader" closeButton><a>Sacola</a> <p>5 itens</p></Modal.Header>

                    <Modal.Body className="modalbody">
                        <div className="infprod">

                            <img className="modalimg" src={prod01} /> 

                            <div className="infprodmod">
                                <a>Rasteira Tira Dedo</a>
                                <p>R$49,99</p>
                            </div>

                            <div className="moreprod">
                                <img className="menos" src={menos} />
                                <a>01</a>
                                <img className="mais" src={mais} />
                            </div>

                            <div className="removeprod">
                                <img className="cross" src={cross} />
                            </div>

                        </div>

                        <div className="infprod">

                            <img className="modalimg" src={prod02} /> 

                            <div className="infprodmod">
                                <a>Rasteira Tira Dedo</a>
                                <p>R$49,99</p>                
                            </div>

                            <div className="moreprod">
                                <img className="menos" src={menos} />
                                <a>01</a>
                                <img className="mais" src={mais} />
                            </div>

                            <div className="removeprod">
                                <img className="cross" src={cross} />
                            </div>

                        </div>

                        <div className="infprod">
                            <img className="modalimg" src={prod03} /> 

                            <div className="infprodmod">
                                <a>Rasteira Tira Dedo</a>
                                <p>R$49,99</p>
                            </div>

                            <div className="moreprod">
                                <img className="menos" src={menos} />
                                <a>01</a>
                                <img className="mais" src={mais} />
                            </div>

                            <div className="removeprod">
                                <img className="cross" src={cross} />
                            </div>
                        </div>
                    </Modal.Body>

                    <div className="freeship"><a>Faltam R$ xx,xx para você <p>Ganhar Frete Grátis</p></a></div>

                    <Modal.Footer className="modfooter">
                        <div>
                            <Button className="buybtn" variant="secondary" onClick={handleClose}><a>Finalizar compra</a></Button>
                        </div>

                        <div className="priceinfo">
                            <a>Total: R$ 149,00</a><p>até 3x de R$ 49,90 sem juros</p>
                        </div>
                    </Modal.Footer>

                </Modal>

            </div>
        </div>
    </div>

</div>

        

        </>
    )

}

export default Navbar;