import './verticalslider.css';
import React, { Component } from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css"; 
import "slick-carousel/slick/slick-theme.css";
import prod02 from './files/prod02.png';
import prod01 from './files/prod01.png';
import play from './files/play.png';
import video from './files/video.png';

export default class CenterMode extends Component {
  render() {
    const settings = {
      customPaging: function(i) {
        return (
          <a>
            <img src={prod02} />
          </a>
        );
      },
      dots: true,
      vertical: true,
      dotsClass: "slick-dots slick-thumb",
      infinite: true,
      speed: 900,
      slidesToShow: 1,
      slidesToScroll: 1
    };
    return (
        <>
      <div className="player">
        <div className="videotxt"><a>Video</a></div>
        <div className="playbtn"><img src={play} /></div>
        <img src={video} />
      </div>  

      <div className="sliderContent">
        <Slider {...settings}>
          <div>
            <img src={prod01} />
          </div>
          <div>
            <img src={prod01} />
          </div>
          <div>
            <img src={prod01} />
          </div>
          <div>
            <img src={prod01} />
          </div>
        </Slider>
      </div>
      </>
    );
  }
}