import './body.css';
import React, { useState } from 'react';
import SimpleSlider from '../../components/verticalslider/';
import Modal from 'react-bootstrap/Modal'
import Button from 'react-bootstrap/Button';
import 'bootstrap/dist/css/bootstrap.min.css';
import prod02 from './bodyfiles/prod02.png';


function Body(){

        const [show, setShow] = useState(false);
      
        const handleClose = () => setShow(false);
        const handleShow = () => setShow(true);

    return(
        <>

    <div className="body">
        <div className="conteiner">
            
            <div className="navpages">
                <a href="#">Home</a>
                <a>/</a>
                <a href="#" className="active">Sapatos</a>
            </div>
            
        
        <div className="maincontent">
            <div className="vertslider">
            <SimpleSlider/> 
            </div>
            
            <div className="prodinfo">
                <div className="prodinfo__content">
                    <h3 className="productname">RASTEIRA TIRA DEDO</h3>
                    <h3 className="productref">RT 0568 | 03.07.0653</h3>
                        <div className="price">
                            <h3 className="pricea">R$69,99 |</h3>
                            <h3 className="priceb">R$55,20</h3>
                            <h3 className="productparc">Ou 6x de R$9,20</h3>
                        </div>

                        <div className="colordefinition">
                            <h3 className="colordef">Cor:</h3>
                            <h3 className="colorname">(Fucsia)</h3>
                        </div>

                        <div className="colors">
                            <div className="selected"><div className="jazzberry"></div></div>
                            <div className="casal"></div>
                            <div className="carmine"></div>
                            <div className="black"></div>
                        </div>

                        <div className="sizedefinition">
                            <a className="sizedef">Tamanho:</a>
                            <a className="sizeselected">(37)</a>
                            <a className="sizeguide" href="#">Guia de medidas</a   >
                        </div>

                        <div className="sizes">
                            <h3 className="size">33</h3>
                            <h3 className="size">34</h3>
                            <h3 className="size">35</h3>
                            <h3 className="size">36</h3>
                            <h3 className="size">37</h3>
                            <h3 className="size">38</h3>
                            <h3 className="size">39</h3>
                            <h3 className="size">40</h3>
                            <h3 className="size">41</h3>
                            <h3 className="size">42</h3>
                        </div>

                        <div className="modalbody">
                            <Button className="btnaddcart" variant="primary" onClick={handleShow}>
                                <a className="txtcart" href="#">ADICIONAR À SACOLA</a>
                            </Button>
                        </div>

                        <div className="infoprod">
                            <a className="inftxt">Rasteira em atanado soft com tira no dedo e fechamento de fivela. Possui sola sempre na cor do cabedal.</a>
                        </div>
                    </div>
            </div>

                




        </div>
        </div>
</div>   


{/* MODAL */}
    <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton>
        </Modal.Header>
        <Modal.Body><img src={prod02} /> </Modal.Body>
        <div className="infomodal"><a>Produto adicionado <p>com sucesso!</p></a></div>
        <Modal.Footer>
            <div><Button className="buybtn" variant="secondary" onClick={handleClose}>
                <a>Finalizar compra</a>
            </Button>
            </div>
            <div><Button className="buymorebtn" variant="primary" onClick={handleClose}>
                <a>Continuar comprando</a>
            </Button>
            </div>
        </Modal.Footer>
      </Modal>      
        </>
    )

}

export default Body;