import './footer.css';
import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import ebit from './footerfiles/ebit.png';
import facebook from './footerfiles/facebook.png';
import instagram from './footerfiles/instagram.png';
import originallogo from './footerfiles/originallogo.png';
import pinteres from './footerfiles/pinteres.png';
import vtexcertified from './footerfiles/vtexcertified.png';



function Footer(){

    return(
        <>
        
        <div className="footer">
            <div className="conteiner">
  
                <div className="footer__main">   
                    <div className="socialCertified">
                        <img src={facebook} />
                        <img src={instagram} />
                        <img src={pinteres} />
                        <img src={vtexcertified} />
                        <img src={ebit} />
                    </div>

                    <div className="footer__menu">
                        <ul>
                            <h3>Institucional</h3>
                            <li><a href="#">A marca</a></li>
                            <li><a href="#">Lojas</a></li>
                            <li><a href="#">Contato</a></li>
                        </ul>
                    </div>

                    <div className="footer__menu">
                        <ul>
                            <h3>Informações</h3>
                            <li><a href="#">Formas de Pagamento</a></li>
                            <li><a href="#">Trocas e Devoluções</a></li>
                            <li><a href="#">Cuidados Com o Produto</a></li>
                        </ul>
                    </div>

                    <div className="footer__menu">
                        <ul>
                            <h3>Conheça</h3>
                            <li><a href="#">Franquias e Multimarcas</a></li>
                            <li><a href="#">Trabalhe com a Gente</a></li>
                            <li><a href="#">Procon-RJ</a></li>
                        </ul>
                    </div>

                    <div className="newsletter">
                        <h3>Assine nossa news</h3>
                        <label>Nome</label>
                        <input placeholder="Nome" type="text"></input>
                        <input placeholder="E-mail" type="email"></input>
                        <button className="send"><a>Enviar</a></button>
                    </div>
                </div>                   
            </div> 

            <div className="footer__copright">
                <div className="conteiner">
                  <span>Lorem iapsum dolor sit amet, consectetur adipiscdsing eleit. Maecenas molestie massa in nunc condimentum, vel platerscerat lacus pulvinar. Suspendisse vel nisl eu tortor feugiat tempus vel in tortor. Nunc semper leo nec tellus gravida faucibus.</span>
                 <img src={originallogo} />
                </div>
            </div> 

        </div>
        </>
    )

}

export default Footer;